import style from './sass/main.scss';
//   
import fetchCountries from './js/fetchCountries';
//    
import debounce from 'lodash.debounce';
//  
import {
  alert,
  defaultModules,
} from '../node_modules/@pnotify/core/dist/PNotify.js';
import * as PNotifyMobile from '../node_modules/@pnotify/mobile/dist/PNotifyMobile.js';
defaultModules.set(PNotifyMobile, {});
//    
import '@pnotify/core/dist/Material.css';

const input = document.querySelector('.input-js');
let list = document.querySelector('.list-js');

input.addEventListener('input', debounce(onSearch, 500));

function onSearch() {
    list.innerHTML = '';
    const searchQuery = input.value;
    const fetchQuery = fetchCountries(searchQuery);

  fetchQuery.then(data => {
      if (data.length >= 10) {
        alert({
          text: '    ',
          styling: 'material',
          type: 'notice',
          mode: 'dark',
          delay: 3000,
        });
      return;
    }

    fetchQuery.then(data => {
      if (data.length === 1) {
        data.map(e =>
          list.insertAdjacentHTML(
            'beforeend',
            `<img src="${e.flags.png}" alt="${e.flags.alt}" width="350px">
            <div>
                <li class="js-li">   - ${e.name.common} </li>
                <li class="js-li">  - ${e.capital} </li>
                <li class="js-li">  - ${e.flag} </li>
                <li class="js-li">  - ${e.population} </li> 
            </div>`,
          ),
        );
      } else {
        fetchQuery.then(data =>
          data.map(e =>
            list.insertAdjacentHTML(
              'beforeend',
                `<div>
              <li class="js-li">
              ${e.name.common} ${e.flag} 
              </li>
              </div>`,
            ),
          ),
        );
      }
    });
  });
};